import React from "react";
import ReactDOM from "react-dom";
import { Firetitan,Wtrtitan,Woodtitan,Lighttitan,Darktitan } from "./setups";

import "./titan-info.css"
function SetTitanInfo(){
return <div className="info-box">
<div className="info-fr">
    <h2>Fire Titan info</h2>
    <p>Fire titan uses sap on round 1.Build your mons(for top row) with lots of resist(at least 60%).</p>
    <a href="#teams">
        <button className="btn-wtr" onClick={Wtrtitan}>Water Team Setup</button>
    </a>

</div>
<div className="info-wtr">
    <h2>Water Titan info</h2>
    <p>Water titan uses attack down on round 1.Build your mons(for top row) with lots of resist(at least
        60%).Wood Hana is a staple for wood team and you should have at least 4(for each team).</p>
    <a href="#teams">
        <button className="btn-wd" onClick={Woodtitan}>Wood Team Setup</button>
    </a>

</div>
<div className="info-wd">
    <h2>Wood Titan info</h2>
    <p>Wood titan uses buff-breaker on round 1.For top row resist isn't necessary and use healers with
        shield.Other buffs such as atk up/def up is not much useful here(L Baby Shark or fire Mildeu can be used
        but it requires certain mons).</p>
    <a href="#teams">
        <button className="btn-fr" onClick={Firetitan}>Fire Team Setup</button>
    </a>

</div>
<div className="info-light">
    <h2>Light Titan info</h2>
    <p>Light titan has very high crit rate and uses blind on round 1.You must use mons with blind(such as L
        Nezha,L Bulbie,Wood Zhu Bajie or healers with blind skill)on both top and bottom row to survive.
        
    </p>
    <a href="#teams">
    <button className="btn-drk" onClick={Darktitan}>Dark Team Setup</button>
    </a>
</div>
<div className="info-drk">
    <h2>Dark Titan info</h2>
    <p>Dark titan uses def down on round 1.You must build your mons with lots of resist(60% - 80%)
        and good amount of def for top row or else it's game over within 2 turns :)
    </p>
    <a href="#teams">
        <button className="btn-light" onClick={Lighttitan}>Light Team Setup</button>
    </a>

</div>
</div>

}

export function TitanInfo(){
    ReactDOM.render(
        <SetTitanInfo/>,
        document.getElementById("titan-info")
    )
}
