import React from "react";
import ReactDOM from "react-dom";

function SetTips() {
    return <div className="titan-tips">
        <h1>
            General tips for titans
        </h1>
        <ul>
            <li><h3>How to build Courageous Strikers</h3></li>
                <ul>
                <li>For early to mid game try- crit rate, atk%, atk%.</li>
                <li>If they lack square or triangle slot then try triple atk%</li>
                <li>For end game, your goal should be atk%, atk%, crit dmg(with high crit rate sub stats).</li>
            </ul>

            <li><h3>How to build Healers/Debuffers</h3></li>
                <ul>
                <li>Build your healers with HP%, def%, recovery%.</li>
                <li>Build debuffers with HP%, HP%, def% or HP%, def%, def%</li>
                </ul>
                <li><h3>Placement for each mon</h3></li>
                <ul>
                <li>Your left side team(for both row)should include upto 2 debuffers/Courageous Strikers(it can be 2 debuffers or 1 debuffer and 1 Courageous Striker).</li>
                <li>Right side
                team(for both row) should include 2 Courageous Strikers.</li>
                <li>There should be 2 healers on each team.2 on the left and 2 on the right(for both rows).</li>
                </ul>
            
                <li><h3>Debuffs</h3></li>
                <ul>
                    <li>Atk down is the must use debuff for every titan.That's why Wood Hana, Dark Venus and Light Baby
                Shark are so popular in titan teams.Make sure you have reliable atk down mons in your team.</li>
                <li>Next most useful debuffs are def down and weaken.</li>
                <li>Blind is very useful against light titan but not useful against others,so keep that in mind.</li>
                <li>For Expose Weakness, attack animation speed is important.As you might know that expose weakness wears off after
                first hit, so make sure your left side expose weakness mon(such as Water Cura or Wood Meowings) has
                the slowest animation speed and your right side attacker(such as L Siegfried,Wood Enkidu,Water
                Balrona) has the fast attack animation speed(you don't your debuffers/healers to steal the expose weakness).</li>
                </ul>
            
                <li><h3>Gem sets</h3></li>
                <ul>
                    <li>For Courageous Strikers- Ruin, Intuition, Valor, Zeal, Leech(for wood titan), Conviction(for
                    dark titan), Indomitability(for
                    dark titan) are preferred but broken sets can be accepted if they have good sub stats.</li>
                    <li>For Debuffers- Conviction, Protection, Life or any broken gems.Their only purpose is to apply debuff and survive.</li>
                    <li>For Healers- Vitality or Salvation are preferred but broken gems will do as long as they have good recovery sub stats.</li>
                    </ul>
                    <li><h3>Enhancements</h3></li>
                <ul>
                    <li>For Courageous Strikers- crit rate, crit dmg, atk%, atk(flat), def(flat), HP(flat), res(optional)</li>
                    <li>For Healers- HP(flat), HP%, def(flat), def%, rec%, rec(flat)/res</li>
                    <li>For Debuffers- HP(flat), HP%, def(flat), def%, res, crit-res/atk(flat)</li>
                    </ul>
        </ul>

    </div>
}

export function Tips() {
    ReactDOM.render(
        <SetTips />,
        document.getElementById("tips")
    )
}
