import React from 'react';

import { Firetitan,Wtrtitan,Lighttitan,Woodtitan } from "./setups";
import { TitanInfo } from './titan-info';
import {Tips} from './tips';
import { About } from './about';
import home from "./images/home.png";
import tips from "./images/Tips_icon.png";
import titanInfo from './images/titan_info.png';
import fire from "./images/fire.png";
import water from "./images/water.png";
import wood from "./images/wood.png";
import light from "./images/light.png";
import dark from "./images/dark.png";
import about_icon from "./images/about.png";

import "./navbar.css"

export function Nav(){
    return <div className="icon">
        <div className="icon-cont">
        <a href="https://msl-titan-guide.netlify.app" onClick={window.location.reload}><img src={home} alt="Home"/></a>
            
            </div>
        
        <div className="icon-cont"> 
        <a href="#tips" onClick={Tips}><img src={tips} alt="Tips"/></a>
        
        </div>
        
        <div className="icon-cont">
            <a href="#titan-info" onClick={TitanInfo}><img src={titanInfo} alt="Titan Info"/></a>
            </div>

        <div className="icon-cont pc-icon">
            <a href="#teams" onClick={Wtrtitan}><img src={fire} alt="Fire Titan"/></a>
            </div>

        <div className="icon-cont pc-icon">
            <a href="#teams" onClick={Woodtitan}><img src={water} alt="Water Titan"/></a>
            </div>
        <div className="icon-cont pc-icon">
            <a href="#teams" onClick={Firetitan}><img src={wood} alt="Wood Titan"/></a>
            </div>
        <div className="icon-cont pc-icon">
            <a href="#teams"><img src={light} alt="Light Titan"/></a>
            </div>
        <div className="icon-cont pc-icon">
            <a href="#teams" onClick={Lighttitan}><img src={dark} alt="Dark Titan"/></a>
            </div>
        
        <div className="icon-cont">
            <a href="#main" onClick={About}><img src={about_icon} alt="About"/></a>
            </div>

            
         
    </div>
    
}

